@media screen and (max-width: 400px) {
  .wrap_hamb_close {
    line-height: 23px !important; } }

.hamb_container {
  display: block;
  position: relative;
  min-height: 400px;
  margin: 30px 0 50px 0;
  padding: 1.8rem .75rem !important;
  overflow: hidden; }

.hamb_wrap_slider {
  display: block;
  position: relative;
  min-height: 500px;
  margin-bottom: 50px;
  margin-top: 20px;
  background-color: #F2F2F2;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s; }

.page_nav_sec {
  display: inline-block;
  position: relative;
  min-height: 25px;
  z-index: 6;
  text-align: left;
  font-size: 11px;
  font-family: 'Bitter', serif; }

.page_nav_sec a,
.page_nav_sec span {
  color: #3149FC;
  cursor: pointer;
  display: inline-block;
  margin: 8px;
  font-size: 13px;
  font-family: 'Bitter', serif;
  -webkit-tap-highlight-color: transparent; }

.page_nav_sec a:hover {
  text-decoration: underline; }

.page_nav_sec a:active {
  border: 0;
  outline: 0;
  -webkit-tap-highlight-color: transparent; }

.page_nav_sec span {
  color: #000;
  cursor: text; }

.hamb_carousel {
  height: 340px !important;
  overflow: hidden; }

.hamb_slide_up_bord {
  position: absolute;
  width: 100%;
  height: 65px;
  top: -25px;
  left: 0;
  background-image: url("../images/home/bord_two.png");
  background-repeat: no-repeat;
  background-size: 100% 80%;
  background-position: 70% 20%; }

.hamb_slide_down_bord {
  position: absolute;
  width: 100%;
  height: 65px;
  bottom: -45px;
  left: 0;
  background-image: url("../images/home/bord_two.png");
  background-repeat: no-repeat;
  background-size: 100% 80%;
  background-position: 70% 20%;
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg); }

.hamb_c_item {
  width: 250px !important;
  height: 260px !important;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.hamb_c_item img {
  width: 220px !important;
  height: 220px !important;
  cursor: pointer; }

.s_prod_title {
  color: #FA8526;
  font-family: 'Nanum Brush Script', cursive;
  font-size: 25px;
  font-weight: bold; }

#hamb_artisan_gc {
  height: 130px !important;
  width: 187px !important;
  margin-top: 50px; }

#hamb_double_beef {
  height: 244px !important; }

/* Product box info */
.wrap_hamb_info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  max-height: 0;
  position: relative;
  width: 350px;
  border: none;
  border-radius: 3px;
  -webkit-transition: 0.4s ease-in;
  -o-transition: 0.4s ease-in;
  transition: 0.4s ease-in;
  opacity: 0;
  padding: .8rem; }

.wrap_hamb_close {
  position: absolute;
  width: 35px;
  height: 35px;
  border: 5px solid #F2F2F2;
  margin-bottom: -30px;
  font-size: 17px;
  text-align: center;
  cursor: pointer;
  line-height: 21px;
  border-radius: 50%;
  top: -10px;
  right: -10px;
  background-color: #F2F2F2;
  -webkit-box-shadow: inset 0px 0px 0px 1px #CCCCCC;
  -moz-box-shadow: inset 0px 0px 0px 1px #CCCCCC;
  box-shadow: inset 0px 0px 0px 1px #CCCCCC;
  -webkit-tap-highlight-color: transparent;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s; }

.wrap_hamb_close:hover {
  font-size: 20px;
  -webkit-box-shadow: inset 0px 0px 0px 1px #909090;
  -moz-box-shadow: inset 0px 0px 0px 1px #909090;
  box-shadow: inset 0px 0px 0px 1px #909090; }

.wrap_hamb_close:active {
  font-size: 24px; }

.s_prod_cal_price {
  display: inline-block;
  height: 30px;
  width: 300px;
  text-align: center; }

.s_prod_cal {
  font-size: 18px;
  position: relative;
  width: 140px;
  height: 30px;
  display: inline-block; }

.s_prod_price {
  position: relative;
  width: 140px;
  height: 30px;
  display: inline-block;
  font-size: 25px;
  font-weight: bold; }

.s_prod_ingred {
  display: block;
  text-align: left;
  width: 300px;
  left: 0 !important;
  margin: 15px 0 5px 0;
  padding-left: 5px;
  padding-bottom: .5rem;
  color: #FA8526;
  font-weight: bold; }

.s_prod_ingred:after {
  display: block;
  clear: both;
  content: "";
  position: relative;
  left: 0;
  bottom: 0;
  max-width: 350px;
  height: 1px;
  width: 270px;
  border-bottom: 1px solid #CCCCCC;
  margin: 0 auto;
  padding: 4px 0px; }

.w_hinfo_ingredients {
  display: inline-block;
  position: relative;
  height: 40px;
  width: 300px;
  text-align: left;
  padding-bottom: 3rem;
  margin: 0 5px 0 5px; }

.w_hinfo_ingredients:after {
  display: block;
  clear: both;
  content: "";
  position: relative;
  left: 0;
  bottom: 0;
  max-width: 300px;
